<template>
    <BaseModal
        name="release"
        :mobileMode="true"
        :closeOutside="true"
        :secondary="true"
        :isLoading="data.loading"
    >
        <template v-slot:title>
            <span>{{ data.item?.title }}</span>
        </template>
        <template v-slot:content>
            <div class="box" v-if="data.item">
                <div class="gallery">
                    <Img
                        v-if="data.item?.cover"
                        :preview="data.item?.cover?.crop"
                        :original="data.item?.cover?.original"
                        :alt="data.item.title"
                        aspectRatio="1 / 1"
                        :skeleton="true"
                        @click="openGallery"
                    />

                    <div class="buttons">
                        <IconButton icon="share" class="secondary size-m"
                            @click="shareIt"
                        />
                        <Button icon="tg-stars" class="secondary size-m"
                            :name="t('common.support')"
                            @click="donate"
                        />
                    </div>
                </div>

                <ul class="static-params">
                    <li v-for="(item, id) in items">
                        <label>{{ t(item.name) }}</label>
                        <i></i>
                        <span>{{ item.value }}</span>
                    </li> 
                </ul>

 
                <Releases
                    v-if="data.releases.length"
                    type="row"
                    :items="data.releases"
                    :source="null"
                    :loadMore="false"
                    title="modals.track.releases"
                    class="inline"
                    handler="release"
                />

                <template v-if="primaryArtists.length">
                    <Artists
                        :items="primaryArtists"
                        type="row"
                        :source="null"
                        :loadMore="false"
                        handler="artist"
                        class="inline"
                        title="blocks.artists.primary"
                    />
                </template>

                <template v-if="featuresArtists.length">
                    <Artists
                        :items="featuresArtists"
                        type="row"
                        :source="null"
                        :loadMore="false"
                        handler="artist"
                        class="inline"
                        title="blocks.artists.features"
                    />
                </template>

                <template v-for="(item, id) in otherArtists">
                    <Artists
                        :items="item.contributors"
                        type="list"
                        :source="null"
                        :loadMore="false"
                        handler="artist"
                        class="inline"
                        :title="item.title"
                    />
                </template>

                <template v-if="data.moods.length">
                    <DictsList
                        title="dicts.moods"
                        :items="data.moods"
                        type="mood"
                    />
                </template>

                <template v-if="data.genres.length">
                    <DictsList
                        title="dicts.genres"
                        :items="data.genres"
                        type="genre"
                    />
                </template>

                <template v-if="data.categories.length">
                    <DictsList
                        title="dicts.categories"
                        :items="data.categories"
                        type="genre"
                    />
                </template>

                <template v-if="data.instruments.length">
                    <DictsList
                        title="dicts.instruments"
                        :items="data.instruments"
                        :clickable="false"
                        type="genre"
                    />
                </template>

                <template v-if="data.tags.length">
                    <DictsList
                        title="dicts.tags"
                        :items="data.tags"
                        :clickable="false"
                        type="genre"
                    />
                </template>
            </div>
        </template>
    </BaseModal>
</template>

<script setup>
import { onMounted, reactive, computed } from 'vue';
import { useI18n } from 'vue-i18n';

import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
import Button from 'ðŸ”—/components/ui/Button.vue';
import Img from 'ðŸ”—/components/ui/Img.vue';
import BaseModal from 'ðŸ”—/components/modals/BaseModal.vue';
import DictsList from '@/components/ui/DictsList.vue';

import Releases from 'ðŸ”—/components/blocks/Releases.vue';
import Artists from 'ðŸ”—/components/blocks/Artists.vue';
import stores from '@/stores';

import api from 'ðŸ”—/api';
import { message, formatDuration } from 'ðŸ”—/utils/utils';
import moment from 'moment';

const { t } = useI18n();

const tg = stores.tg();
const modals = stores.modals();
const share = stores.share();
const nav = stores.nav();

const data = reactive({
    loading: true,
    categories: [],
    instruments: [],
    tags: [],
    moods: [],
    genres: []
});

const shareIt = () => {
    share.track(data.item, { type: 'release', release: data.releases[0] });

    setTimeout(() => {
        close();
    }, 400);
}


const items = computed(() => {
    if (!data?.item?.id) {
        return;
    }

    const result = [
        {
            value: data.item.fromProject,
            name: 'modals.track.from-project'
        },
        {
            value: formatDuration(data.item.duration),
            name: 'modals.track.duration'
        },
        {
            value: !data.item.releaseDate ? null :
                moment(data.item.releaseDate).format('DD MMMM YYYY'),
            name: 'modals.track.release-date'
        },
        {
            value: data.item.instrumental ?
                t('modals.track.yes') :
                t('modals.track.no'),
            name: 'modals.track.instrumental'
        },
        {
            value: data.item.instrumental ? null :
                data.item.language,
            name: 'modals.track.language'
        },

        {
            value: data.item.isExplicit ?
                t('modals.track.yes') :
                t('modals.track.no'),
            name: 'modals.track.is-explicit'
        },
        {
            value: data.item.isRadioEdit ?
                t('modals.track.yes') :
                t('modals.track.no'),
            name: 'modals.track.is-radio-edit'
        },
        {
            value: data.item.isCover ?
                t('modals.track.yes') :
                t('modals.track.no'),
            name: 'modals.track.is-cover'
        },
        {
            value: data.item.contentId ?
                t('modals.track.yes') :
                t('modals.track.no'),
            name: 'modals.track.content-id'
        },
        {
            value: data.item.hasProRights ?
                t('modals.track.yes') :
                t('modals.track.no'),
            name: 'modals.track.has-pro-rights'
        },
        {
            value: data.item.bpm,
            name: 'modals.track.bpm'
        },
    ];

    return result.filter(item => item.value !== null);
});

// Contributors
    const primaryArtists = computed(() => {
        if (!data.item?.contributors?.length) {
            return [];
        }

        return data.item.contributors.filter(item => item.role === 'PRIMARY' && item.state === 'CONFIRMED').map(item => item.artist);
    });

    const featuresArtists = computed(() => {
        if (!data.item?.contributors?.length) {
            return [];
        }

        return data.item.contributors.filter(item => item.role === 'FEATURED' && item.state === 'CONFIRMED').map(item => item.artist);
    });

    const otherArtists = computed(() => {
        return [
            {
                role: 'PRODUCER',
                title: 'blocks.artists.producer'
            },
            {
                role: 'REMIXER',
                title: 'blocks.artists.remixer'
            },
            {
                role: 'COMPOSER',
                title: 'blocks.artists.composer'
            },
            {
                role: 'MIXER',
                title: 'blocks.artists.mixer'
            },
            {
                role: 'MASTERING_ENGINEER',
                title: 'blocks.artists.mastering-engineer'
            },
            {
                role: 'LYRICIST',
                title: 'blocks.artists.lyricist'
            }
        ].map(payload => ({
            title: payload.title,
            contributors: data.item.contributors.filter(item => item.role === payload.role && item.state === 'CONFIRMED').map(item => item.artist)
        })).filter(item => !!item.contributors?.length)
    });


const openGallery = () => {
    if (!data.item?.id || !data.item?.cover?.original) {
        return;
    }

    modals.open('gallery', {
        quietClose: true,
        data: { imgs: [{
            title: data.item.title,
            ...data.item?.cover
        }] } });
}

const donate = () => {
    message.info(t('common.soon'));
}

const close = () => {
    modals.close('track');
};

const load = async () => {
    if (!data.item?.dicts?.length) {
        data.loading = false;
        return;
    }

    data.loading = true;

    const types = {};

    (await api.dict().getTypes())?.forEach(item => types[item.code] = item.id);

    Object.keys(types).forEach(type => {
        if (data[type]) {
            data[type] = data.item.dicts.filter(item => item.dictId === types[type]).map(item => ({ ...item, name: item.title })) || [];
        }
    });

    data.loading = false;
}

onMounted(async () => {
    const payload = modals.getData('track');

    if (!payload?.id) {
        close();
        return;
    }

    if (payload.item) {
        data.item = payload.item;
    } else  {
        data.item = await api.tracks().get(payload.id);
    }

    if (payload.releases) {
        data.releases = payload.releases;
    } else {
        const releases = await api.releases().list({ tracks: [payload.id] });
        if (releases?.items) {
            data.releases = releases.items;
        }
    }

    await load();

    data.loading = false;
});

</script>

<style lang="scss" scoped>
    .box {
        display: flex;
        flex-direction: column;
        gap: 32px;
        padding: var(--block-gap) 0;

        .gallery {
            position: relative;
            .buttons {
                position: absolute;
                right: 16px;
                bottom: 16px;
                display: flex;
                flex-direction: row;
                gap: 8px;
            }
        }

        .static-params {
            margin: 0;
            padding: 0;
            display: flex;
            flex-direction: column;
            gap: 8px;
            li {
                display: flex;
                flex-direction: row;
                align-items: baseline;
                gap: 8px;
                span {
                    flex-shrink: 0;
                    font: var(--font-body-2);
                    color: var(--dark-500);
                }

                i {
                    flex: 1;
                    border-bottom: dashed 1px var(--dark-20);
                }

                label {
                    flex-shrink: 0;
                    font: var(--font-caption);
                    color: var(--dark-80);
                }
            }
        }
    }
</style>
